import axios, {BASEURL} from '../../utils/axios'
import {getUserId} from "@/utils/util";

/**
 * 当日运动界面详情
 * @returns {*}
 */
export function getHeartRateDetail(measureTimeDesc) {
  return axios.get(`/monitor/monitor/HeartRate/SyLogHeartRateHistory/getHeartRateDetail`,{
    params:{
      measureTimeDesc,
    }
  });
}


// 历史数据
export function getListByStartEnd(startTime,endTime) {
  return axios.get(`/monitor/monitor/HeartRate/SyLogHeartRateHistory/getListByStartEnd`,{
    params:{
      startTime, endTime, userId: getUserId()
    }
  });
}

// 添加运动数据
export function addHeartrateRecord(heartRate,measureTimeDesc,day) {
  const data = []
  const params = { heartRate, measureTimeDesc, day, userId: getUserId()  }
  data.push(params)
  return axios.post(`/monitor/monitor/HeartRate/SyLogHeartRateHistory/insert`,data);
}

// 添加运动数据
export function getSyLogSportHistoryByTime(startTime,endTime) {
  return axios(`/monitor/monitor/HeartRate/SyLogHeartRateHistory/getListByStartEnd`,{
    params: { startTime, endTime,userId: getUserId()}
  });
}
